import { Component, OnInit } from '@angular/core';
import { EnvironmentSettingsService } from '../service/enviroment-settings.service';


@Component({
  selector: 'app-rigs',
  templateUrl: './rigs.component.html',
  styleUrls: ['./rigs.component.scss']
})
export class RigsComponent implements OnInit {

  imgPath = '';
  modalImg = '';
  playVideo = true;

  constructor(
    private envSettings: EnvironmentSettingsService
  ) { }

  ngOnInit() {
// determine image path
this.imgPath = this.envSettings.getServiceBase();

  }

  openModel(image) {
    this.modalImg = image;
    setTimeout(() => {
      document.getElementById('ticketModal').style.display = 'block';
    }, 500);
    
  }

}
